export default {
	//免费注册
	data() {
		return {
			countdowntext:"验证码",
			wait:60,
			disabled:false
		}
	},
	methods: {
		//验证码
		countdown(){
			this.time()
		},
		//验证码的秒数
		time(){
			let times = null
			this.disabled = true
			//这块点击反复执行定时器
			// clearInterval(times)
			let {countdowntext,wait} = this.$data
			// console.log(countdowntext,wait)
				times = setInterval(()=>{
					wait--
					// console.log(wait)
					this.countdowntext = wait
					if(wait==0){
						this.disabled = false
						countdowntext = "重新获取验证码"
						clearInterval(times)
						this.countdowntext = countdowntext
						this.wait = 60
					}
					
				},1000)
		},
		//提交
		smslogin(e){
			let {input,password} = e.detail.value
			// console.log(input,password)
			if(input && password){
				//设置缓存
				uni.setStorage({
				    key: 'registered',
				    data: {
						"input":input,
						"password":password
					},
				    success(res){
						console.log(res)
					},
					fail(err){
						console.log(err)
					}
				});
			}
		}
	}
}
